import T from 'ant-design-vue/es/table/Table'

export default {
    data () {
        return {
            localLoading: false,
            localDataSource: [],
            localPagination: Object.assign({}, this.pagination)
        }
    },
    props: Object.assign({}, T.props, {
        rowKey: {
            type: [String, Function],
            default: 'key'
        },
        data: {
            type: Function,
            required: true
        },
        pageNum: {
            type: Number,
            default: 1
        },
        pageSize: {
            type: Number,
            default: 10
        },
        showSizeChanger: {
            type: Boolean,
            default: true
        },
        size: {
            type: String,
            default: 'default'
        },
        /**
         * alert: {
         *   show: true,
         *   clear: Function
         * }
         */
        alert: {
            type: [Object, Boolean],
            default: null
        },
        rowSelection: {
            type: Object,
            default: null
        },
        /** @Deprecated */
        showAlertInfo: {
            type: Boolean,
            default: false
        },
        showPagination: {
            type: String | Boolean,
            default: 'auto'
        },
        /**
         * enable page URI mode
         *
         * e.g:
         * /users/1
         * /users/2
         * /users/3?queryParam=test
         * ...
         */
        pageURI: {
            type: Boolean,
            default: false
        }
    }),
    watch: {
        'localPagination.current' (val) {
            this.pageURI && this.$router.push({
                ...this.$route,
                name: this.$route.name,
                params: Object.assign({}, this.$route.params, {
                    pageNo: val
                })
            })
        },
        pageNum (val) {
            Object.assign(this.localPagination, {
                current: val
            })
        },
        pageSize (val) {
            Object.assign(this.localPagination, {
                pageSize: val
            })
        },
        showSizeChanger (val) {
            Object.assign(this.localPagination, {
                showSizeChanger: val
            })
        }
    },
    created () {
        const { pageNo } = this.$route.params
        const localPageNum = this.pageURI && (pageNo && parseInt(pageNo)) || this.pageNum
        this.localPagination = ['auto', true].includes(this.showPagination) && Object.assign({}, this.localPagination, {
            current: localPageNum,
            pageSize: this.pageSize,
            showSizeChanger: this.showSizeChanger
        }) || false
        // console.log('this.localPagination', this.localPagination)
        this.loadData()
    },
    methods: {
        /**
         * è¡¨æ ¼é‡æ–°åŠ è½½æ–¹æ³•
         * å¦‚æžœå‚æ•°ä¸º true, åˆ™å¼ºåˆ¶åˆ·æ–°åˆ°ç¬¬ä¸€é¡µ
         * @param Boolean bool
         */
        refresh (bool = false) {
            bool && (this.localPagination = Object.assign({}, {
                current: 1, pageSize: this.pageSize
            }))
            this.loadData()
        },
        /**
         * åŠ è½½æ•°æ®æ–¹æ³•
         * @param {Object} pagination åˆ†é¡µé€‰é¡¹å™¨
         * @param {Object} filters è¿‡æ»¤æ¡ä»¶
         * @param {Object} sorter æŽ’åºæ¡ä»¶
         */
        loadData (pagination, filters, sorter) {
            this.localLoading = true
            const parameter = Object.assign(
                {
                    offset: (Number((pagination && pagination.current) || this.showPagination && this.localPagination.current) - 1) * 10 || (Number(this.pageNum) - 1) * 10,
                    limit: (pagination && pagination.pageSize) || this.showPagination && this.localPagination.pageSize || this.pageSize
                },
                (sorter && sorter.field && { sortField: sorter.field }) || {},
                (sorter && sorter.order && { sortOrder: sorter.order }) || {},
                { ...filters }
            )
            // console.log('parameter', parameter)
            const result = this.data(parameter)
            // å¯¹æŽ¥è‡ªå·±çš„é€šç”¨æ•°æ®æŽ¥å£éœ€è¦ä¿®æ”¹ä¸‹æ–¹ä»£ç ä¸­çš„ r.pageNo, r.totalCount, r.data
            // eslint-disable-next-line
        if ((typeof result === 'object' || typeof result === 'function') && typeof result.then === 'function') {
                result.then(r => {
                    this.localPagination = this.showPagination && Object.assign({}, this.localPagination, {
                        current: parameter.offset / 10 + 1, // è¿”å›žç»“æžœä¸­çš„å½“å‰åˆ†é¡µæ•°
                        total: r.total, // è¿”å›žç»“æžœä¸­çš„æ€»è®°å½•æ•°
                        showSizeChanger: this.showSizeChanger,
                        pageSize: (pagination && pagination.pageSize) || this.localPagination.pageSize }) || false
                    // ä¸ºé˜²æ­¢åˆ é™¤æ•°æ®åŽå¯¼è‡´é¡µé¢å½“å‰é¡µé¢æ•°æ®é•¿åº¦ä¸º 0 ,è‡ªåŠ¨ç¿»é¡µåˆ°ä¸Šä¸€é¡µ
                    if (r.list && r.list.length === 0 && this.showPagination && this.localPagination.current > 1) {
                        this.localPagination.current--
                        this.loadData()
                        return
                    }

                    // è¿™é‡Œç”¨äºŽåˆ¤æ–­æŽ¥å£æ˜¯å¦æœ‰è¿”å›ž r.totalCount ä¸” this.showPagination = true ä¸” pageNo å’Œ pageSize å­˜åœ¨ ä¸” totalCount å°äºŽç­‰äºŽ pageNo * pageSize çš„å¤§å°
                    // å½“æƒ…å†µæ»¡è¶³æ—¶ï¼Œè¡¨ç¤ºæ•°æ®ä¸æ»¡è¶³åˆ†é¡µå¤§å°ï¼Œå…³é—­ table åˆ†é¡µåŠŸèƒ½
                    try {
                        if ((['auto', true].includes(this.showPagination) && r.totalCount <= (r.pageNo * this.localPagination.pageSize))) {
                            this.localPagination.hideOnSinglePage = true
                        }
                    } catch (e) {
                        this.localPagination = false
                    }
                    // console.log('loadData -> this.localPagination', this.localPagination)
                    this.localDataSource = r.list || r // è¿”å›žç»“æžœä¸­çš„æ•°ç»„æ•°æ®
                    this.localLoading = false
                })
            }
        },
        initTotalList (columns) {
            const totalList = []
            columns && columns instanceof Array && columns.forEach(column => {
                if (column.needTotal) {
                    totalList.push({
                        ...column,
                        total: 0
                    })
                }
            })
            return totalList
        },
    },

    render () {
        const props = {}
        const localKeys = Object.keys(this.$data)

        Object.keys(T.props).forEach(k => {
            const localKey = `local${k.substring(0, 1).toUpperCase()}${k.substring(1)}`
            if (localKeys.includes(localKey)) {
                props[k] = this[localKey]
                return props[k]
            }
            this[k] && (props[k] = this[k])
            return props[k]
        })
        const table = (
            <a-table {...{ props, scopedSlots: { ...this.$scopedSlots } }} onChange={this.loadData} onExpand={ (expanded, record) => { this.$emit('expand', expanded, record) } }>
                { Object.keys(this.$slots).map(name => (<template slot={name}>{this.$slots[name]}</template>)) }
            </a-table>
        )

        return (
            <div class="table-wrapper">
                { table }
            </div>
        )
    }
}
